import swal from 'sweetalert'

swal.addCart = () => {
  swal('', {
    button: false,
    timer: 1200,
  })
}

swal.addCollection = () => {
  swal('', {
    button: false,
    timer: 1200,
  })
}

swal.popUpMessage = async (message) => {
  await swal(message, {
    button: false,
    timer: 1200,
  })
  // return after message disappear
}

swal.confirmDelete = async (func, ...params) => {
  const result = await swal({
    title: '',
    icon: 'warning',
    buttons: ['', ''],
    dangerMode: true,
  })
  if (!result) return
  swal({
    title: '',
    button: '',
    icon: 'success',
  })
  func(...params)
}

swal.pleaseLogin = () => {
  swal({
    title: '',
  })
}

export default swal
