import React from 'react'
import '../style.scss'
import { Accordion, Card } from 'react-bootstrap'

function ProductAccordion({ item }) {
  const method = [
    {
      title: '',
      one: '',
      two: '',
      three: '',
    },
  ]
  return (
    <>
      <Accordion className="official__accordion" defaultActiveKey="1">
        <Card>
          <div>
            <Accordion.Toggle as={Card.Header} variant="link" eventKey="1">
              
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="1">
              <Card.Body>{item.description_zh}</Card.Body>
            </Accordion.Collapse>
          </div>
        </Card>
        <Card>
          <div>
            <Accordion.Toggle as={Card.Header} variant="link" eventKey="0">
              
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
              <Card.Body className="official__accordion-text">
                <span>{item.top}</span>
                <span>{item.middle}</span>
                <span>{item.base}</span>
              </Card.Body>
            </Accordion.Collapse>
          </div>
        </Card>
        <Card>
          {method.map((item, index) => {
            return (
              <div key={index}>
                <Accordion.Toggle as={Card.Header} variant="link" eventKey="2">
                  {item.title}
                </Accordion.Toggle>
                <Accordion.Collapse eventKey="2">
                  <Card.Body className="official__accordion-text">
                    <span>1. {item.one}</span>
                    <span>2. {item.two}</span>
                    <span>3. {item.three}</span>
                  </Card.Body>
                </Accordion.Collapse>
              </div>
            )
          })}
        </Card>
      </Accordion>
    </>
  )
}

export default ProductAccordion
