import React from 'react'
import { Link } from 'react-router-dom'
import baseUrl from '../../../config/index'
import './style.css'
import imgURL from './imgs/LICORNE_logo.svg'

function Footer() {
  return (
    <>
      <footer className="footer">
        <div className="footer__logoBox">
          <Link to={baseUrl}>
            <img className="footer__logo" src={imgURL} alt="" />
          </Link>
        </div>
        <div className="footer__listBox">
          <ul className="footer__topList">
            <li>
              {/* <Link to="/"></Link> */}
              
            </li>
            <li>
              <Link to={baseUrl + '/custom/entrance'}></Link>
            </li>
            <li>
              {/* <Link to="/course"></Link> */}
              
            </li>
            <li>
              <Link to={baseUrl + '/bestseller'}></Link>
            </li>
          </ul>
          <ul className="footer__bottomList">
            <li>
              {/* <Link to="/official"></Link> */}
              
            </li>
            <li>
              {/* <Link to="/quiz"></Link> */}
              
            </li>
            <li>
              {/* <Link to="/"></Link> */}
              
            </li>
          </ul>
        </div>
        <div className="footer__inputBox">
          <input
            className="footer__subscribe"
            type="text"
            placeholder=""
          />
          <input
            className="footer__subButton"
            type="submit"
            value=""
          />
        </div>
        <div className="footer__Text">
          <p className="footer__copyRight">
            Copyright  Licorne Co., 2021. All rights reserved.
          </p>
        </div>
      </footer>
    </>
  )
}

export default Footer
